  position: relative;
}
.hero.is-primary {
    background-color: transparent !important;
}
.hero:after {
    position: absolute;
    z-index: -1;
    content: "";
    background-image: url(/shogi-player/static/img/hero.2d210b6.jpg);
    background-position: center;
    background-size: cover;
    opacity: 1.0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
